.menu {
    position: fixed;
    z-index: 999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 55px;
    padding: 12px;
    background-color: black;
    &__imglogo {
      width: 90px;
    }
    &__imgnav {
      width: 38px;
      @include tablet {
        display: none;
      }
    }
    &__nav {
      display: none;
      height: 12px;

      &--1, &--2 {
        display: flex;
        align-items: center; 
      } 
      &--1 {
        border-right: 1px solid white;
      }

      &--item {
        font-size: 11px;
        font-weight: 700;
        color: white;
        padding: 0 20px;
      }
      @include tablet {
        display: flex;
      }
    }
  }

